.nav-style {
	z-index: 10;
	position: fixed;
	font-family: 'Titillium Web', sans-serif;

	ul {
		height: 70px;
		display: flex;
		list-style-type: none;
		justify-content: space-between;
		align-items: center;
		width: 100vw;
		box-sizing: border-box;
		padding: 10px 20px;
		background-color: rgb(34, 39, 60);
		border-bottom: 1px solid rgb(200, 200, 200);
	}

	.menu {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.logo {
		width: 120px;
	}

	.options {
		display: flex;
		align-items: center;
	}

	li {
		margin-right: 20px;

		&:last-child {
			margin-right: 0;
		}
	}

	.dropdown-toggle {
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;

		.img-user {
			margin-left: 10px;
		}
	}
	
	.dropdown-toggle::after {
		display: none;
	}

	.dropdown-menu {
		width: 100%;
	}

	li img {
		border-radius: 50%;
		width: 45px;
		display: block;
		object-fit: contain;
	}

	.header {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.options li {
		color: #fff;
		margin-right: 3px;
		
		&:last-child {
			margin-right: 0;
		}

		.bell {
			font-size: 18px;
			cursor: pointer;
		}

		button:focus {outline: 0;}


		.bell-down {
			margin-right: 200px;
		}
	}

	.header img {
		margin-right: 20px;
	}

	ul .login {
		display: flex;
		align-items: space-between;
	}

	.login-link {
		color: #fff;

		&:hover {
			text-decoration: none;
			color: rgb(200, 200, 200);
		}
	}
}
